body {
	background-color: #222529;
	display: flex;
	justify-content: center;
	align-items: center;
	padding-top: 10%;
}

#root {
	padding: 20px;
}

.MainContainer {
	height: 610px;
	width: 410px;
	padding: 5px;
}

.Display {
	height: 100px;
	border-style: solid;
	border-width: thin;
	border-color: darkgrey;
}

.CurrentDisplay,
.HistoricalDisplay {
	height: 50px;
	display: flex;
	align-items: flex-end;
	justify-content: flex-end;
	padding-right: 10px;
	padding-bottom: 10px;
	font-size: x-large;
}

.ButtonGroupOne {
	height: 500px;
	width: 300px;
}

.RectangleButton {
	height: 100px;
	width: 200px;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: x-large;
}

.VerticalRectangleButton {
	height: 200px;
	width: 100px;
	display: flex;
	justify-content: center;
	align-items: center;
	border-style: solid;
	border-width: thin;
	border-color: darkgrey;
	font-size: x-large;
}

.SquareButton {
	height: 100px;
	width: 100px;
	border-style: solid;
	border-width: thin;
	border-color: darkgrey;
	font-size: x-large;
	display: flex;
	justify-content: center;
	align-items: center;
}
